.car-card {
  border: 1px solid #ccc;
  padding: 20px;
  margin: 10px 0;
  border-radius: 8px;
}

.car-card img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto 10px;
}

.car-card h2 {
  font-size: 20px;
  margin: 0 0 10px;
}

.car-card p {
  margin: 5px 0;
}